import { FC, useState } from "react"
import { SubmitHandler, useForm } from "react-hook-form"
import { useTranslation } from "react-i18next"
import { useNavigate } from "react-router-dom"
import { Button, Input, Space } from "@illa-design/react"
import "./SignUpPage.css"

interface SignUpFields {
  nickname: string
  email: string
  password: string
  confirmPassword: string
}

const SignUpPage: FC = () => {
  console.log("rodou a pagina")
  const { t } = useTranslation()
  const navigate = useNavigate()
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm<SignUpFields>()
  const [loading, setLoading] = useState(false)

  const onSubmit: SubmitHandler<SignUpFields> = async (data) => {
    setLoading(true)
    try {
      // Aqui você pode chamar a função para realizar o cadastro
      console.log(data)
      // Supondo que o cadastro foi bem-sucedido
      navigate("/login")
    } catch (error) {
      // Tratar erro
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  const password = watch("password")

  return (
    <div className="signup-page-container">
      <div className="signup-form-container">
        <form onSubmit={handleSubmit(onSubmit)}>
          <Space direction="vertical" size="large">
            <div>
              <label>{t("page.user.sign_up.field.nickname")}</label>
              <Input
                {...register("nickname", {
                  required: t("page.user.sign_up.error_message.nickname.required"),
                })}
                onChange={(value) =>
                  register("nickname").onChange({ target: { value } })
                }
              />
              {errors.nickname && (
                <span style={{ color: "red" }}>{errors.nickname.message}</span>
              )}
            </div>

            <div>
              <label>{t("page.user.sign_up.field.email")}</label>
              <Input
                type="email"
                {...register("email", {
                  required: t("page.user.sign_up.error_message.email.required"),
                  pattern: {
                    value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                    message: t("page.user.sign_up.error_message.email.invalid"),
                  },
                })}
                onChange={(value) =>
                  register("email").onChange({ target: { value } })
                }
              />
              {errors.email && (
                <span style={{ color: "red" }}>{errors.email.message}</span>
              )}
            </div>

            <div>
              <label>{t("page.user.sign_up.field.password")}</label>
              <Input
                type="password"
                {...register("password", {
                  required: t("page.user.sign_up.error_message.password.required"),
                  minLength: {
                    value: 6,
                    message: t(
                      "page.user.sign_up.error_message.password.min_length",
                    ),
                  },
                })}
                onChange={(value) =>
                  register("password").onChange({ target: { value } })
                }
              />
              {errors.password && (
                <span style={{ color: "red" }}>{errors.password.message}</span>
              )}
            </div>

            <div>
              <label>{t("page.user.sign_up.field.confirm_password")}</label>
              <Input
                type="password"
                {...register("confirmPassword", {
                  required: t(
                    "page.user.sign_up.error_message.confirm_password.required",
                  ),
                  validate: (value) =>
                    value === password ||
                    t("page.user.sign_up.error_message.confirm_password.match"),
                })}
                onChange={(value) =>
                  register("confirmPassword").onChange({ target: { value } })
                }
              />
              {errors.confirmPassword && (
                <span style={{ color: "red" }}>
                  {errors.confirmPassword.message}
                </span>
              )}
            </div>

            <Button type="submit" loading={loading}>
              {t("page.user.sign_up.button")}
            </Button>
          </Space>
        </form>
      </div>
    </div>
  )
}

export default SignUpPage
